:root {
  letter-spacing: -.02em;
  --primary-text-color: #fff;
  --primary-text-color-05: rgba(5, 5, 5, .5);
  --primary-text-color-02: rgba(5, 5, 5, .2);
  --primary-text-color-01: #050505;
  --primary-color-btn: #9bb537;
  --bgc-background: #161616;
  --bgc-background-mob-menu: #050505;
  --border-paginatio: rgba(5, 5, 5, .3);
  --transition-dur-func-btn-hover: 250ms cubic-bezier(.4, 0, .2, 1) 0ms;
  font-family: Inter, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.container {
  min-width: 320px;
  max-width: 375px;
  padding: 0 20px;
}

.home-wraper {
  justify-content: space-between;
  display: flex;
}

.filter-bar, .all-categories-popular-recipes {
  display: block;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .home-wraper {
    flex-direction: row;
    display: flex;
  }

  .filters-recipes-pagination {
    margin-left: 48px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }
}

.section-header {
  position: relative;
}

.container-header {
  justify-content: space-between;
  align-items: center;
  padding-top: 28px;
  display: flex;
}

.nav-header {
  display: none;
}

.logo-green {
  color: var(--primary-color-btn);
  text-align: center;
  letter-spacing: -.64px;
  font-size: 16px;
}

.logo-black {
  color: var(--bgc-background-mob-menu);
  line-height: 1.12;
}

.buttons-header {
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.icon-burger, .icon-trash {
  stroke: var(--bgc-background-mob-menu);
}

.icon-trash {
  margin-right: 14px;
}

.logo-green, .nav-list-mob, .nav-list {
  transition: transform, opacity .3s cubic-bezier(.4, 0, .2, 1);
}

.logo-green:hover, .nav-list-mob:hover, .nav-list:hover {
  filter: drop-shadow(0 3px 3px rgba(0, 0, 0, .15));
  transform: translateY(-2px)scale(1.1);
}

.button-trash, .button-burger {
  width: 28px;
  height: 24px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  gap: 14px;
}

.button-trash {
  margin-top: 3px;
}

.icon-trash:hover, .icon-burger:hover, .icon-trash:focus, .icon-burger:focus {
  stroke: var(--primary-color-btn);
}

.mobile-menu {
  height: 100vh;
  width: 55%;
  background: var(--primary-color-btn);
  padding-top: 200px;
  transition: transform .3s;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.mobile-menu.show-mob {
  transform: translateX(0);
}

.button-close {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.mob-nav-title {
  color: var(--primary-text-color);
  font-size: 12px;
  line-height: normal;
}

.nav-list-mob:not(:last-child) {
  margin-bottom: 16px;
}

.nav-list-mob {
  text-align: center;
  margin-bottom: 200px;
}

.switcher-burger {
  align-items: center;
  display: flex;
}

.switch {
  width: 46px;
  height: 20px;
  align-items: center;
  margin-left: 20px;
  position: relative;
}

.switch input {
  display: none;
}

.slider-header {
  cursor: pointer;
  background: linear-gradient(#cecdcd 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider-header:hover, .slider-header:focus {
  transform: scale(1.03);
}

.slider-header:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--primary-text-color);
  transition: all .4s;
  position: absolute;
  top: 1px;
  left: 1px;
}

input:checked + .slider-header {
  background: linear-gradient(#9bb537, rgba(155, 181, 55, .69) 0%);
}

input:checked + .slider-header:before {
  transform: translateX(26px);
}

.slider-header.round {
  border-radius: 34px;
}

.slider-header.round:before {
  border-radius: 50%;
}

.buttons-header .switcher-burger {
  display: none;
}

@media only screen and (min-width: 768px) {
  .button-burger {
    display: none;
  }

  .buttons-header .switcher-burger {
    display: flex;
  }

  .buttons-header {
    flex-direction: row-reverse;
  }

  .nav-header {
    display: block;
  }

  .nav-section {
    display: flex;
  }

  .nav-list.active a {
    color: var(--primary-color-btn);
  }

  .nav-list:not(.active) a {
    color: var(--bgc-background-mob-menu);
  }

  .nav-title-header:hover, .nav-title-header:focus {
    color: var(--primary-color-btn);
  }

  .logo-green {
    font-size: 18px;
  }

  .nav-title-header {
    color: var(--bgc-background-mob-menu);
    font-size: 12px;
  }

  .nav-list:not(:last-child) {
    margin-right: 16px;
  }
}

@media screen and (min-width: 1280px) {
  .container-header {
    padding-top: 40px;
  }
}

.hero-wrapper {
  max-width: 335px;
  text-align: left;
  margin-bottom: 48px;
  padding-top: 137px;
}

.hero-title {
  text-transform: uppercase;
  color: var(--primary-text-color-01);
  margin-bottom: 16px;
  font-family: Inter;
  font-size: 26px;
  font-weight: 600;
  line-height: 120%;
}

.hero-title span {
  color: var(--primary-color-btn);
  font-style: italic;
  font-weight: 400;
}

.hero-text {
  color: var(--primary-text-color-01);
  font-style: regular;
  letter-spacing: .02em;
  margin-bottom: 28px;
  font-size: 14px;
  line-height: 18px;
}

.hero-btn {
  width: 111px;
  height: 37px;
  cursor: pointer;
  color: var(--primary-text-color-01);
  border: 1px solid var(--primary-color-btn);
  transition: color var(--transition-dur-func-btn-hover), background-color var(--transition-dur-func-btn-hover);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  margin-bottom: 48px;
  font-family: Inter;
  font-size: 14px;
  display: inline-block;
}

.hero-btn:hover, .hero-btn:focus {
  color: var(--primary-text-color);
  background-color: var(--primary-color-btn);
  border: 1px solid var(--primary-color-btn);
}

.slider {
  width: 355px;
  overflow: hidden;
}

.slider img {
  height: 100%;
  width: 100%;
  display: block;
}

.slider-item-cook {
  width: 80px;
  height: 280px;
}

.slider-item-cook img {
  object-fit: cover;
  border-radius: 15px;
}

.slider-item-topic {
  width: 200px;
  height: 280px;
  background-color: #050505;
  border-radius: 15px;
  position: relative;
}

.slider-item-topic img {
  width: 150px;
  height: 150px;
  border-radius: 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-item-topic .description {
  width: 257px;
  color: rgba(248, 248, 248, .6);
  text-align: center;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  position: absolute;
  top: 88%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-item-topic .country {
  color: rgba(248, 248, 248, .2);
  font-family: Inter;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  position: absolute;
  top: 94%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.slider-item-finally {
  width: 222px;
  height: 280px;
  border-radius: 15px;
  overflow: hidden;
}

.slider-item-finally img {
  width: 529px;
  height: 529px;
  object-position: -170px -145px;
  object-fit: cover;
  background: #050505;
}

.slick-track {
  min-width: 0;
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.slick-slide img {
  display: block;
}

.slick-list {
  margin-bottom: 20px;
  overflow: hidden;
}

.slick-dots {
  justify-content: center;
  align-items: center;
  gap: 2px;
  margin-bottom: 80px;
  display: flex;
}

.slick-dots li {
  list-style: none;
}

.slick-dots button {
  width: 12px;
  height: 12px;
  background-color: #000;
  border: 1px;
  border-radius: 50%;
  font-size: 0;
}

.slick-dots li.slick-active button {
  background-color: #9bb537;
}

@media screen and (min-width: 768px) {
  .hero-container {
    max-width: 744px;
  }

  .hero-wrapper {
    max-width: 446px;
  }

  .hero-title {
    font-size: 36px;
  }

  .hero-text {
    width: 408px;
    font-size: 16px;
    line-height: 24px;
  }

  .slider {
    width: 736px;
  }

  .slick-track {
    gap: 16px;
  }

  .slider-item-cook {
    width: 137px;
    height: 442px;
  }

  .slider-item-topic, .slider-item-finally {
    width: 351px;
    height: 442px;
  }

  .slider-item-topic img {
    width: 304px;
    height: 271px;
  }

  .slider-item-finally img {
    width: 663px;
    height: 663px;
    object-position: -120px -125px;
    object-fit: cover;
    background: #050505;
  }
}

@media screen and (min-width: 1280px) {
  .hero-container {
    gap: 32px;
    display: inline-flex;
  }

  .hero-wrapper {
    margin-bottom: 0;
  }

  .hero-title {
    width: 446px;
  }

  .slider {
    width: 702px;
    padding-top: 100px;
  }

  .slider-wrapper {
    width: 1180px;
  }
}

@media screen and (min-width: 320px) {
  .all-categories-btn {
    width: 176px;
    height: 50px;
    background-color: var(--primary-text-color);
    cursor: pointer;
    color: var(--primary-text-color-05);
    border: 1px solid var(--primary-text-color-02);
    border-radius: 15px;
    margin-bottom: 24px;
    margin-left: 20px;
    padding: 14px 28px;
    font-family: Inter;
    font-size: 18px;
    font-weight: 500;
  }

  .btn.btn-active {
    background-color: var(--primary-color-btn);
    border: 1px solid var(--primary-color-btn);
    color: #f8f8f8;
  }

  .all-categories-list {
    width: 326px;
    height: 210px;
    flex-direction: column;
    align-items: flex-start;
    gap: 18px;
    margin-left: 20px;
    display: flex;
    overflow-y: scroll;
  }

  .all-categories-item-btn:hover, :focus {
    color: var(--primary-color-btn);
  }

  .all-categories-item-btn {
    color: var(--border-paginatio);
    cursor: pointer;
    background: none;
    border: none;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28571;
  }

  ::-webkit-scrollbar {
    width: 8px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: rgba(5, 5, 5, .18);
    border-radius: 12px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: var(--primary-color-btn);
  }
}

@media screen and (min-width: 768px) {
  .all-categories-list {
    width: 176px;
    height: 416px;
    gap: 24px;
  }

  .all-categories-btn {
    margin-bottom: 30px;
  }

  .all-categiries-list-container {
    width: 176px;
    height: 416px;
  }

  .all-categories-item-btn {
    font-size: 16px;
    line-height: 1.25;
  }
}

@media screen and (min-width: 1280px) {
  .all-categories-btn {
    margin-bottom: 42px;
  }

  .all-categiries-list-container {
    width: 235px;
    height: 416px;
  }

  .all-categories-list {
    width: 235px;
    height: 416px;
    gap: 24px;
  }
}

.time-area {
  flex-direction: row;
  display: flex;
}

.filter-bar-btn-reset {
  gap: 4px;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .all-inputs {
    flex-wrap: wrap;
    display: flex;
  }

  .time-label, .area-label, .ingredient-label {
    height: 48px;
    width: 188px;
    flex-direction: column;
    gap: 10px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  body {
    overflow-x: hidden;
  }

  body::-webkit-scrollbar {
    width: 12px;
  }

  body::-webkit-scrollbar-thumb {
    border: 2px solid var(--light-color);
    background: rgba(5, 5, 5, .18);
    border-radius: 12px;
    transition: all .25s;
  }

  body.dark-theme::-webkit-scrollbar-thumb {
    border: 2px solid var(--dark-color);
    background: rgba(255, 255, 255, .2);
    transition: all .25s;
  }

  body::-webkit-scrollbar-thumb:hover {
    background: var(--primary-color);
    transition: all .25s;
  }

  .gallery-container {
    width: 782px;
  }

  .container {
    overflow: visible;
  }

  .gallery-filters {
    width: 1280px;
  }
}

:root {
  --primary-color: #9bb537;
  --light-color: #f8f8f8;
  --dark-color: #161616;
  --white-color: #fff;
  --black-color: #050505;
}

.filters-span {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  transition: all .25s;
  display: block;
}

.dark-theme .filters-span, .dark-theme .search-input::-ms-input-placeholder {
  color: rgba(255, 255, 255, .5);
  transition: all .25s;
}

.dark-theme .filters-span, .dark-theme .search-input::placeholder {
  color: rgba(255, 255, 255, .5);
  transition: all .25s;
}

.search-label {
  width: 100%;
  margin-bottom: 14px;
  position: relative;
}

.search-input {
  color: var(--black-color);
  width: 100%;
  height: 46px;
  background: none;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  padding: 14px 48px;
  transition: all .25s;
}

.dark-theme .search-input {
  color: var(--white-color);
  border: 1px solid rgba(255, 255, 255, .2);
  transition: all .25s;
}

.search-input:focus {
  outline: 0;
}

.search-input::-ms-input-placeholder {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  transition: all .25s;
}

.search-input::placeholder {
  color: rgba(5, 5, 5, .5);
  margin-bottom: 14px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  transition: all .25s;
}

.search-icon {
  fill: rgba(5, 5, 5, .5);
  transition: all .25s;
  position: absolute;
  top: 36px;
  left: 18px;
}

.search-icon.active, .dark-theme .search-icon.active {
  fill: var(--primary-color);
}

.dark-theme .search-icon {
  fill: rgba(255, 255, 255, .5);
  transition: all .25s;
}

.reset-btn {
  width: 18px;
  height: 18px;
  cursor: pointer;
  background: inherit;
  border: 0;
  display: none;
  position: absolute;
  top: 36px;
  right: 15px;
}

.close-search-icon {
  stroke: #9bb537;
  fill: var(--primary-color-btn);
  transition: all .25s;
}

.dark-theme .close-search-icon {
  stroke: var(--white-color);
  transition: all .25s;
}

.time-label, .area-label, .ingredient-label {
  height: 68px;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.time-label {
  width: 160px;
  margin-bottom: 14px;
  margin-right: 14px;
}

.area-label {
  width: 161px;
}

.ingredient-label {
  width: 188px;
}

.dark-theme .time-input, .dark-theme .area-input, .dark-theme .ingredient-input {
  color: rgba(255, 255, 255, .5);
  border: 1px solid rgba(255, 255, 255, .2);
  transition: all .25s;
}

.time-input, .area-input, .ingredient-input {
  height: 46px;
  cursor: pointer;
  border: 1px solid rgba(5, 5, 5, .2);
  border-radius: 15px;
  transition: all .25s;
  display: flex;
  position: relative;
}

.time-input {
  width: 160px;
}

.area-input {
  width: 161px;
}

.ingredient-input {
  width: 188px;
}

.time-placeholder, .area-placeholder, .ingredient-placeholder {
  width: 150px;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  color: rgba(5, 5, 5, .5);
  cursor: pointer;
  background: none;
  border: 0;
  padding-left: 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  transition: all .25s;
  overflow: hidden;
}

.dark-theme .time-placeholder, .dark-theme .area-placeholder, .dark-theme .ingredient-placeholder {
  color: rgba(255, 255, 255, .5);
  transition: all .25s;
}

.time-placeholder.active, .area-placeholder.active, .ingredient-placeholder.active, .time-placeholder:hover, .area-placeholder:hover, .ingredient-placeholder:hover {
  color: var(--black-color);
  transition: all .25s;
}

.dark-theme .time-placeholder.active, .dark-theme .area-placeholder.active, .dark-theme .ingredient-placeholder.active, .dark-theme .time-placeholder:hover, .dark-theme .area-placeholder:hover, .dark-theme .ingredient-placeholder:hover {
  color: var(--white-color);
  transition: all .25s;
}

.time-placeholder:focus, .area-placeholder:focus, .ingredient-placeholder:focus {
  background-color: rgba(0, 0, 0, 0);
  outline: none;
}

.select-icon {
  fill: rgba(0, 0, 0, 0);
  stroke: rgba(5, 5, 5, .5);
  transition: all .25s;
  position: absolute;
  top: 14px;
  right: 14px;
}

.time-icon.active, .area-icon.active, .ingredient-icon.active {
  transition: all .25s;
  transform: rotate(180deg);
}

.dark-theme .select-icon {
  stroke: rgba(255, 255, 255, .5);
  transition: all .25s;
}

.time-list, .area-list, .ingredient-list {
  z-index: 2;
  background: var(--white-color);
  opacity: 0;
  pointer-events: none;
  border-radius: 8px;
  flex-direction: column;
  transition: all .25s;
  display: flex;
  position: absolute;
  top: 46px;
  left: -1px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.dark-theme .time-list, .dark-theme .area-list, .dark-theme .ingredient-list {
  background: var(--black-color);
  transition: all .25s;
}

.time-list.active, .area-list.active, .ingredient-list.active {
  opacity: 1;
  pointer-events: auto;
  transition: all .25s;
}

.time-list {
  width: 160px;
  height: 124px;
}

.area-list {
  width: 161px;
  height: 176px;
}

.ingredient-list {
  width: 188px;
  height: 176px;
}

.time-list::-webkit-scrollbar, .area-list::-webkit-scrollbar, .ingredient-list::-webkit-scrollbar {
  width: 14px;
}

.time-list::-webkit-scrollbar-thumb, .area-list::-webkit-scrollbar-thumb, .ingredient-list::-webkit-scrollbar-thumb {
  height: 69px;
  border: 4px solid var(--white-color);
  background: rgba(5, 5, 5, .1);
  border-radius: 12px;
}

.dark-theme .time-list::-webkit-scrollbar-thumb, .dark-theme .area-list::-webkit-scrollbar-thumb, .dark-theme .ingredient-list::-webkit-scrollbar-thumb {
  border: 4px solid var(--black-color);
  background: rgba(255, 255, 255, .1);
}

.time-list::-webkit-scrollbar-thumb:hover, .area-list::-webkit-scrollbar-thumb:hover, .ingredient-list::-webkit-scrollbar-thumb:hover {
  background: var(--black-color);
}

.dark-theme .time-list::-webkit-scrollbar-thumb:hover, .dark-theme .area-list::-webkit-scrollbar-thumb:hover, .dark-theme .ingredient-list::-webkit-scrollbar-thumb:hover {
  background: var(--white-color);
}

.area-list::-webkit-scrollbar-thumb {
  height: 55px;
}

.time-item, .area-item, .ingredient-item {
  text-align: left;
  cursor: pointer;
  color: rgba(5, 5, 5, .3);
  background: none;
  border: none;
  padding-top: 8px;
  padding-left: 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
}

.dark-theme .time-item, .dark-theme .area-item, .dark-theme .ingredient-item {
  color: rgba(255, 255, 255, .3);
}

.time-item:hover, .area-item:hover, .ingredient-item:hover {
  color: var(--black-color);
}

.dark-theme .time-item:hover, .dark-theme .area-item:hover, .dark-theme .ingredient-item:hover {
